body {
  max-width: 1920px;
  width: 100%;
  background-color: #fff;
  justify-content: center;
  font-size: 16px;
}

.swiper__hero {
  width: 100%;
  min-height: 820px;
  height: 100%;
  position: absolute;
  top: 0;
  overflow: hidden;
}

.img-slide__mob {
  display: none;
}

.wrap__slide {
  z-index: 1;
  justify-content: space-between;
  transition: transform 1.2s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.left-slide {
  position: relative;
}

.wrap__img {
  object-fit: cover;
  position: relative;
}

.wrap__rigth-slide {
  height: 100%;
  background-position: 100%;
  transition: transform 1s;
  animation: animationRight 1s linear infinite alternate forwards;
  display: block;
  position: relative;
}

.wrap__left-slide {
  background-position: 0;
  transition: transform 1.5s;
  animation: animationLeft 1.5s linear infinite alternate forwards;
  display: block;
  position: relative;
}

.title__right {
  max-width: 1920px;
  width: 100%;
  height: 100%;
  color: #fff;
  text-transform: uppercase;
  background-color: rgba(47, 48, 58, .4);
  padding-top: 300px;
  padding-left: 0%;
  font-family: Fiver;
  font-size: 130px;
  font-weight: 400;
  line-height: .5;
  position: absolute;
  top: 0%;
}

.title__left {
  width: 100%;
  height: 100%;
  color: #fff;
  text-transform: uppercase;
  text-align: right;
  background-color: rgba(47, 48, 58, .4);
  padding-top: 300px;
  font-family: Fiver;
  font-size: 130px;
  font-weight: 400;
  line-height: .5;
  position: absolute;
  top: 0%;
}

.img__left-slide, .wrap__img {
  position: relative;
}

.wrap__left-slide {
  height: 100%;
  background-position: 0;
  transition: transform 1.5s;
  animation: animationLeft 1.5s linear infinite alternate forwards;
  display: block;
  position: relative;
}

.wrap__rigth-slide, .wrap__left-slide {
  background-position: 0;
  display: block;
  position: relative;
}

.wrap__rigth-slide {
  background-position: 100%;
}

.img-slide__desc {
  position: relative;
}

