  }

  .hero__inner {
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px;
    display: flex;
    position: relative;
  }

  .logo__content {
    z-index: 32;
    position: absolute;
  }

  .logo__desctop {
    display: none;
  }

  .logo__mob {
    flex-direction: column;
    align-items: center;
    gap: 5px;
    display: flex;
  }

  .logo__text-desctop {
    color: #fff;
    font-family: deserta, sans-serif;
    font-size: 36px;
    line-height: 1.2;
  }

  .logo__text-mob {
    color: #fff;
