    font-size: 25px;
    line-height: 1.2;
  }

  .icon__phone-desc {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .menu-button__list {
    width: 100%;
    z-index: 1;
    height: 100%;
    text-transform: uppercase;
    flex-flow: column wrap;
    justify-content: center;
    gap: 70px;
    padding: 30px 0;
    display: flex;
    position: relative;
  }

  .list-item__text {
    color: #fff;
    font-size: 30px;
    line-height: 1.2;
  }

  .menu-button__item {
    width: 100%;
    width: 131px;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    display: flex;
  }

  .menu__icon-mob {
    display: block;
  }

  .menu__icon {
    display: none;
  }

  .menu__button {
    color: #fff;
    text-align: center;
    letter-spacing: .06em;
    cursor: pointer;
    background: none;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.2;
    display: inline-block;
  }

  .menu-button__item:after {
    content: "";
    width: 18px;
    height: 18px;
    background: url("polygon.428d0314.svg") center no-repeat;
    display: block;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 150%);
  }

  .menu-button__item:nth-child(5):after, .menu-button__item:nth-child(6):after, .menu-button__item:nth-child(3):after {
    transform: translate(-50%, -150%);
  }

  .menu-button__item:nth-child(1):after {
    transform: translate(-50%, 270%);
  }

  .menu-button__item:nth-child(1) {
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
  }

  .menu-button__item:nth-child(2) {
    position: absolute;
    top: 35%;
    left: 0%;
  }

  .menu-button__item:nth-child(3) {
    position: absolute;
    top: 65%;
    left: 70%;
  }

  .menu-button__item:nth-child(4) {
    position: absolute;
    top: 35%;
    left: 70%;
  }

  .menu-button__item:nth-child(5) {
    position: absolute;
    top: 80%;
    left: 50%;
    transform: translateX(-50%);
  }

  .menu-button__item:nth-child(6) {
    position: absolute;
    top: 65%;
    left: 0%;
  }
}

@media screen and (min-width: 380px) and (min-width: 560px) and (max-width: 700px) {
  .menu-button__item:nth-child(2), .menu-button__item:nth-child(6) {
    left: 50px;
  }
}

@media screen and (min-width: 380px) and (min-width: 701px) and (max-width: 900px) {
